.reservation {
  &__panel-control {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
  }

  .authorization {

    * {
      color: #ffffff;
      font-weight: 500;
      font-size: 18px;
      line-height: 21px;
      margin-left: 30px;
      transition: $transition;

      &:hover {
        color: $accent-color;
      }
    }

  }
}