.table {
  &__table-header {
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    display: flex;
    margin-bottom: 30px;
    margin-left: 65px;

    p {
      margin: 0;
    }
  }

  &__header-item {
    max-width: 293px;
    width: 100%;
    text-align: center;
    padding-bottom: 14px;

    p {
      font-weight: 500;
      font-size: 18px;
      line-height: 21px;
      color: #ffffff;

      span {
        color: $accent-color;
      }
    }
  }

  table {
    .reserved {
      background-color: #C4C4C4;
      color: #868686;


      &:before {
        background-color: #868686;
      }

      &:hover {
        background-color: #C4C4C4;
      }
    }

    .reserving {
      background-color: #fff;
      color: $accent-color;

      &:before {
        background-color: $accent-color;
      }

      &:hover {
        background-color: #fff;
      }
    }

    .reserving-time {
      color: $accent-color;
    }

    .reservation-selection {
      width: 100%;
      max-width: 100%;
      position: absolute;
      right: -100%;
      z-index: 1;
      padding: 0 20px;

      .cancel {
        color: #ffffff;
        margin-top: 12px;
        position: relative;
        display: inline-block;

        &:before {
          position: absolute;
          content: '';
          height: 1px;
          width: 100%;
          background-color: #fff;
          bottom: 0;
          transition: $transition;
        }

        &:hover {
          &:before {
            width: 0;
          }
        }
      }
    }


    tr {
      td {
        width: 229px;
        border-right: 1px solid rgba(255, 255, 255, 0.2);
        border-top: 1px solid rgba(255, 255, 255, 0.2);
        padding: 0;
        height: 40px;
        color: #ffffff;
        font-size: 18px;
        font-weight: 500;
        cursor: pointer;
        transition: $transition;
        position: relative;
        text-align: center;
        vertical-align: middle;

        &:before {
          position: absolute;
          content: '';
          height: 100%;
          width: 10px;
          left: 0;
          top: 0;
        }

        &:first-child {
          cursor: default;
          width: 50px;
          border-top: none;

          &:hover {
            background: none;
          }
        }

        &:hover {
          background-color: rgba(225, 225, 225, 0.15);
        }
      }

      &:last-child {
        td {
          border-bottom: 1px solid rgba(255, 255, 255, 0.2);

          &:first-child {
            border-bottom: none;
          }
        }
      }

      &:nth-child(2n) {
        td {
          border-top: 1px dashed rgba(255, 255, 255, 0.2);

          &:first-child {
            border-top: none;
          }
        }
      }

      .time-label {
        padding: 0;
      }
    }

    .schedule-rows td[data-selected],
    .schedule-rows td[data-selecting] {
      background-color: #fff;
      position: relative;
      border: none;

      &:before {
        position: absolute;
        content: '';
        width: 10px;
        height: 100%;
        left: 0;
        background-color: $accent-color;
      }
    }

    .schedule-rows td[data-disabled] {
      background-color: rgba(225, 225, 225, 0.1);
    }


    .table table .schedule-rows td[data-disabled] {
      background-color: rgb(225, 225, 225);
    }

  }
}











